import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { StrategyService } from 'src/app/service/strategy.service';
import { ValidityValidator } from './validity.validator';

@Component({
  selector: 'kt-create-strategy',
  templateUrl: './create-strategy.component.html',
  styleUrls: ['./create-strategy.component.scss']
})
export class CreateStrategyComponent implements OnInit {
  editFlag: boolean;

  strategyForm = new FormGroup({
    strategy_name: new FormControl,
    slippage: new FormControl,
    commision: new FormControl,
    symbol: new FormControl,
    status: new FormControl,
    validity_start: new FormControl,
    validity_end: new FormControl,

  });
  constructor(
    public dialogRef: MatDialogRef<CreateStrategyComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private fb: FormBuilder,
    private strategyService: StrategyService,


  ) { }

  ngOnInit(): void {
    console.log(this.data);
    this.strategyForm = this.fb.group({
      strategy_name: ['', Validators.required],
      slippage: ['', Validators.compose([
				Validators.required,
        Validators.min(0),
				Validators.max(100),
        
			]),],
      commision: ['', Validators.compose([
				Validators.required,
				Validators.min(0),
			]),],
      symbol: ['', Validators.required],
      status: ['', Validators.required],
      validity_start: ['', Validators.required],
      validity_end: ['', Validators.required],
    }, {
			validator: ValidityValidator.CheckValidity
    });
    if (this.data.idstrategy){
      var strategy = this.data;
      console.log("EDIT");
      this.editFlag = true;
      this.strategyForm.patchValue({
        strategy_name: strategy?.strategy_name,
        slippage: strategy?.slippage,
        commision: strategy?.commision,
        symbol: strategy?.strategy_name,
        status: strategy?.status,
        validity_start: strategy?.validity_start,
        validity_end: strategy?.validity_end,
      })
    } else {
      console.log("new");
      this.editFlag = false;
    }
  }
  save(){
    if (this.strategyForm.invalid) {
      window.alert("Please input all fields correctly!");
      return;
    }
    if (this.editFlag){
      this.strategyService.updateStrategy(
        this.data.idstrategy,
        this.strategyForm.value.strategy_name,
        this.strategyForm.value.slippage,
        this.strategyForm.value.commision,
        this.strategyForm.value.symbol,
        this.strategyForm.value.status,
        this.strategyForm.value.validity_start,
        this.strategyForm.value.validity_end,
        ).then((result:any) => {
        if (result.status == 'error'){
          window.alert("Something wrong happened!");
        } else {
          window.alert("successfully created!");
          this.dialogRef.close();
        }
        })
    } else {
      this.strategyService.createStrategy(
        this.strategyForm.value.strategy_name,
        this.strategyForm.value.slippage,
        this.strategyForm.value.commision,
        this.strategyForm.value.symbol,
        this.strategyForm.value.status,
        this.strategyForm.value.validity_start,
        this.strategyForm.value.validity_end,
        ).then((result:any) => {
        if (result.status == 'error'){
          window.alert("Something wrong happened!");
        } else {
          window.alert("successfully created!");
          this.dialogRef.close();
        }
        })
    }

  }
  cancel() {
    this.dialogRef.close();
  }

}
