import { ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { StrategyService } from 'src/app/service/strategy.service';

@Component({
  selector: 'kt-assign-strategy',
  templateUrl: './assign-strategy.component.html',
  styleUrls: ['./assign-strategy.component.scss']
})
export class AssignStrategyComponent implements OnInit {

  availableStrategy = [];
  constructor(
    public dialogRef: MatDialogRef<AssignStrategyComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public strategyService: StrategyService,
		private changeDetectorRefs: ChangeDetectorRef,

  ) { }

  ngOnInit(): void {
    console.log(this.data);
    this.strategyService.getAllStrategies().then((result:any) => {
      console.log(result.result);
      result.result.map((strategy:any) => {
        if (strategy.status == "active") {
          this.strategyService.checkAssigned(this.data.idusers,strategy.idstrategy ).then((result:any) => {
            console.log(result.result);
            if (result.result.length) {
              strategy["assigned"] = true 
            }
            else {
              strategy["assigned"] = false 
            }
            console.log( strategy["assigned"]);

              this.availableStrategy.push(strategy);
              this.changeDetectorRefs.detectChanges();
          })
        }
      })
    })
  }
  cancel(){
    this.dialogRef.close();
  }
  save(){
    console.log(this.availableStrategy);
    console.log(this.data.idusers);
    var strategyByUserList = [];
    this.availableStrategy.forEach((strategy:any) => {
      if(strategy.assigned) {
        strategyByUserList.push(strategy.idstrategy)
      }
    });
    console.log(strategyByUserList);
    this.strategyService.saveAssignedStrategiesByUser(this.data.idusers, strategyByUserList).then(result => {
      console.log(result);
      window.alert("successfully updated!");
      this.dialogRef.close();
    })
  }

  // getAssignByUser(strategy){
  //   console.log(strategy.idstrategy);
    
  //   console.log(this.data.idusers);
  //   this.strategyService.checkAssigned(this.data.idusers,strategy.idstrategy ).then((result:any) => {
  //     console.log(result.result);
  //   })
  //   return true;
  // }
}
