

import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment'

@Injectable({
    providedIn: 'root'
})
export class TradeService {

    constructor(private http: HttpClient) { }
    baseURL = environment.baseURL;

    getAllTrades() {
        return new Promise((resolve, reject) => {
            this.http
                .get(this.baseURL + '/trade/getAllTrades')
                .subscribe(
                    json => {
                        resolve(json);
                    },
                    error => {
                        reject(error);
                    }
                );
        });
    }
    getAllTradesByUser(idusers) {
        return new Promise((resolve, reject) => {
            this.http
                .post(this.baseURL + '/trade/getAllTradesByUser', {idusers: idusers})
                .subscribe(
                    json => {
                        resolve(json);
                    },
                    error => {
                        reject(error);
                    }
                );
        });
    }
}

