// Angular
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CommonModule } from '@angular/common';
import { NgbDropdownModule, NgbTabsetModule, NgbTooltipModule } from '@ng-bootstrap/ng-bootstrap';
// Core Module
import { CoreModule } from '../../../core/core.module';
import { PartialsModule } from '../../partials/partials.module';
import { DashboardComponent } from './dashboard.component';
import { MatRadioModule } from '@angular/material/radio';
import { MatIconModule } from '@angular/material/icon';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatAutocompleteModule } from '@angular/material/autocomplete';
import { MatSnackBarModule } from '@angular/material/snack-bar';
import { MatInputModule } from '@angular/material/input';
import { MatPaginatorModule } from '@angular/material/paginator';
import { MatProgressSpinnerModule } from '@angular/material/progress-spinner';
import { MatSortModule } from '@angular/material/sort';
import { MatTableModule } from '@angular/material/table';
import { MatSelectModule } from '@angular/material/select';
import { MatMenuModule } from '@angular/material/menu';
import { MatProgressBarModule } from '@angular/material/progress-bar';
import { MatButtonModule } from '@angular/material/button';
import { MatCheckboxModule } from '@angular/material/checkbox';
import { MatDialogModule, MAT_DIALOG_DEFAULT_OPTIONS } from '@angular/material/dialog';
import { MatTabsModule } from '@angular/material/tabs';
import { MatNativeDateModule } from '@angular/material/core';
import { MatCardModule } from '@angular/material/card';
import { MatStepperModule } from '@angular/material/stepper';
import { MatTooltipModule } from '@angular/material/tooltip';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { NgxMatFileInputModule } from '@angular-material-components/file-input';
import { UsersComponent } from './users/users.component';
import { StrategiesComponent } from './strategies/strategies.component';
import { CreateUserComponent } from './users/create-user/create-user.component';
import { CreateStrategyComponent } from './strategies/create-strategy/create-strategy.component';
import { AssignStrategyComponent } from './users/assign-strategy/assign-strategy.component';
import { TestComponent } from './test/test.component';
import { TradesComponent } from './trades/trades.component';

const routes: Routes = [
  {
	path: '',
	component: DashboardComponent,
  },
  {
    path: 'users',
    component: UsersComponent,
  },
  {
    path: 'strategies',
    component: StrategiesComponent,
  },
  {
    path: 'test',
    component: TestComponent,
  },
  {
    path: 'trade',
    component: TradesComponent,
  },
];

@NgModule({
  imports: [
    NgxMatFileInputModule,
    FormsModule,
		ReactiveFormsModule,
    CommonModule,
    PartialsModule,
    CoreModule,
    RouterModule.forChild(routes),
    // ng-bootstrap modules
    NgbDropdownModule,
    NgbTabsetModule,
    NgbTooltipModule,

    MatButtonModule,
		MatMenuModule,
		MatSelectModule,
        MatInputModule,
		MatTableModule,
		MatAutocompleteModule,
		MatRadioModule,
		MatIconModule,
		MatNativeDateModule,
		MatProgressBarModule,
		MatDatepickerModule,
		MatCardModule,
		MatPaginatorModule,
		MatSortModule,
		MatCheckboxModule,
		MatProgressSpinnerModule,
		MatSnackBarModule,
		MatTabsModule,
    MatTooltipModule,
    MatStepperModule
  ],
  providers: [],
  declarations: [
    DashboardComponent,
    UsersComponent,
    StrategiesComponent,
    CreateUserComponent,
    CreateStrategyComponent,
    AssignStrategyComponent,
    TestComponent,
    TradesComponent,
  ],
  entryComponents: [
    CreateUserComponent,
    CreateStrategyComponent,
    AssignStrategyComponent
  ]
})
export class DashboardModule {
}
