import { Inject } from '@angular/core';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { UserService } from 'src/app/service/user.service';

@Component({
  selector: 'kt-create-user',
  templateUrl: './create-user.component.html',
  styleUrls: ['./create-user.component.scss']
})
export class CreateUserComponent implements OnInit {
  hide: boolean;
  editFlag: boolean;
  userForm = new FormGroup({
    name: new FormControl,
    email: new FormControl,
    role: new FormControl,
    password: new FormControl,
    description: new FormControl,
  });

  constructor(
    public dialogRef: MatDialogRef<CreateUserComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private fb: FormBuilder,
    private userService: UserService,

    
  ) { }

  ngOnInit(): void {
    console.log("this.data");

    console.log(this.data);
    this.userForm = this.fb.group({
      name: ['', Validators.required],
      email: ['', Validators.compose([Validators.required, Validators.email])],
      role: ['', Validators.required],
      password: ['', Validators.required],
      description: ['', Validators.required],
    });
    if (this.data.user){
      var user = this.data.user;
      console.log("EDIT");
      this.editFlag = true;
      this.userForm.setValue({
        name: user?.username,
        email: user?.email,
        role: user?.role,
        password: user?.password,
        description: user?.description,
      })
    } else {
      console.log("new");
      this.editFlag = false;
    }
  }

  save(){
    if (this.userForm.invalid) {
      window.alert("Please input all fields correctly!");
      return;
    }
    if (this.editFlag){
      this.userService.updateUser(this.data.user.idusers, this.userForm.value.name,this.userForm.value.email, this.userForm.value.password, this.userForm.value.description, this.userForm.value.role).then((result: any) =>{
        console.log(result);
        if (result.status == 'error'){
          window.alert("this email already used by another user!");
        } else {
          window.alert("successfully created!");
          this.dialogRef.close();
        }
      })
    } else {
      this.userService.createUserByAdmin(this.userForm.value.name,this.userForm.value.email, this.userForm.value.password, this.userForm.value.description, this.userForm.value.role).then((result: any) =>{
        console.log(result);
        if (result.status == 'error'){
          window.alert("this email already used by another user!");
        } else {
          window.alert("successfully created!");
          this.dialogRef.close();
        }
      })
    }


  }
  cancel() {
    this.dialogRef.close();
  }

}
