

import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment'

@Injectable({
    providedIn: 'root'
})
export class StrategyService {

    constructor(private http: HttpClient) { }
    baseURL = environment.baseURL;

    updateStrategy(idstrategy, strategy_name, slippage, commision, symbol, status, validity_start, validity_end) {
        return new Promise((resolve, reject) => {
            this.http
                .post(this.baseURL + '/strategy/updateStrategy',
                    {
                        idstrategy: idstrategy,
                        strategy_name: strategy_name,
                        slippage: slippage,
                        commision: commision,
                        symbol: symbol,
                        status: status,
                        validity_start: validity_start,
                        validity_end: validity_end,
                    })
                .subscribe(
                    json => {
                        resolve(json);
                    },
                    error => {
                        reject(error);
                    }
                );
        });
    }

    createStrategy(strategy_name, slippage, commision, symbol, status, validity_start, validity_end) {
        return new Promise((resolve, reject) => {
            this.http
                .post(this.baseURL + '/strategy/createStrategy',
                    {
                        strategy_name: strategy_name,
                        slippage: slippage,
                        commision: commision,
                        symbol: symbol,
                        status: status,
                        validity_start: validity_start,
                        validity_end: validity_end,
                    })
                .subscribe(
                    json => {
                        resolve(json);
                    },
                    error => {
                        reject(error);
                    }
                );
        });
    }

    getAllStrategies() {
        return new Promise((resolve, reject) => {
            this.http
                .get(this.baseURL + '/strategy/getAllStrategies')
                .subscribe(
                    json => {
                        resolve(json);
                    },
                    error => {
                        reject(error);
                    }
                );
        });
    }
    
    getAllStrategiesByUser(idusers) {
        return new Promise((resolve, reject) => {
            this.http
                .post(this.baseURL + '/strategy/getAllStrategiesByUser', {idusers: idusers})
                .subscribe(
                    json => {
                        resolve(json);
                    },
                    error => {
                        reject(error);
                    }
                );
        });
    }

    checkAssigned(idusers, idstrategy) {
        return new Promise((resolve, reject) => {
            this.http
                .post(this.baseURL + '/strategy/checkAssigned', {idusers: idusers, idstrategy: idstrategy})
                .subscribe(
                    json => {
                        resolve(json);
                    },
                    error => {
                        reject(error);
                    }
                );
        });
    }
    saveAssignedStrategiesByUser(idusers, strategies) {
        return new Promise((resolve, reject) => {
            this.http
                .post(this.baseURL + '/strategy/saveAssignedStrategiesByUser', {idusers: idusers, strategies: strategies})
                .subscribe(
                    json => {
                        resolve(json);
                    },
                    error => {
                        reject(error);
                    }
                );
        });
    }
}

