import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatDialog } from '@angular/material/dialog';
import { MatTableDataSource } from '@angular/material/table';
import { CreateStrategyComponent } from './create-strategy/create-strategy.component';
import { StrategyService } from 'src/app/service/strategy.service';
import { Router } from '@angular/router';

@Component({
  selector: 'kt-strategies',
  templateUrl: './strategies.component.html',
  styleUrls: ['./strategies.component.scss']
})
export class StrategiesComponent implements OnInit {
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) set matPaginator(mp: MatPaginator) {
    this.paginator = mp;
  }
  dataSource: MatTableDataSource<any>;
  displayedColumns = ['idstrategy','strategy_name', 'slippage', 'commision', 'symbol', 'validity_start', 'validity_end', 'status'];
  pageIndex: number = 0;
  length: number = 10;
  userRole;

  constructor(
    public dialog: MatDialog,
    public strategyService: StrategyService,
    private router: Router,

  ) { }

  ngOnInit(): void {

    if(window.localStorage.getItem("role")){
      this.userRole = window.localStorage.getItem("role")
    }
    else {
      this.router.navigate(['auth/login']); // Main page
    }
    if (this.userRole == 'admin') {
      this.strategyService.getAllStrategies().then((strategies:any) => {
        console.log(strategies);
        this.dataSource = new MatTableDataSource(strategies.result);
        this.setDataSourceAttributes();
      })
    } else {
      this.strategyService.getAllStrategiesByUser(window.localStorage.getItem("userID")).then((strategies:any) => {
        console.log(strategies);
        this.dataSource = new MatTableDataSource(strategies);
        this.setDataSourceAttributes();
      })
    }




  }

  setDataSourceAttributes() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  create_starategy(){
    const dialogRef = this.dialog.open(CreateStrategyComponent, { data: {} });
    dialogRef.afterClosed().subscribe(result => {
      this.ngOnInit();
      // window.alert("Successfully saved!")
    })
  }

  onPaginateChange(event) {
    this.pageIndex = event.pageIndex;
    this.length = event.pageSize;
  }

  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

  strategyDetail(strategy){
    if (this.userRole == "admin"){
      console.log(strategy);
      const dialogRef = this.dialog.open(CreateStrategyComponent, { data: strategy });
      dialogRef.afterClosed().subscribe(result => {
        this.ngOnInit();
        // window.alert("Successfully saved!")
      })
    }
  }

}
