import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatDialog } from '@angular/material/dialog';
import { MatTableDataSource } from '@angular/material/table';
import { TradeService } from 'src/app/service/trade.service';
import { Router } from '@angular/router';

@Component({
  selector: 'kt-trades',
  templateUrl: './trades.component.html',
  styleUrls: ['./trades.component.scss']
})
export class TradesComponent implements OnInit {
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) set matPaginator(mp: MatPaginator) {
    this.paginator = mp;
  }
  dataSource: MatTableDataSource<any>;
  displayedColumns = ['user', 'strategy', 'direction', 'entry_time', 'entry_price', 'size', 'exit_price', 'exit_time', 'realized_pnl'];
  pageIndex: number = 0;
  length: number = 10;
  userRole;

  constructor(
    public tradeService: TradeService,
    private router: Router,

    
  ) { }

  ngOnInit(): void {
    if(window.localStorage.getItem("role")){
      this.userRole = window.localStorage.getItem("role")
    }
    else {
      this.router.navigate(['auth/login']); // Main page
    }
    if (this.userRole == 'admin') {
      this.displayedColumns = ['user', 'strategy', 'direction', 'entry_time', 'entry_price', 'size', 'exit_price', 'exit_time', 'realized_pnl'];
      this.tradeService.getAllTrades().then((strategies:any) => {
        console.log(strategies);
        this.dataSource = new MatTableDataSource(strategies.result);
        this.setDataSourceAttributes();
      })
    } else {
      this.displayedColumns = ['strategy', 'direction', 'entry_time', 'entry_price', 'size', 'exit_price', 'exit_time', 'realized_pnl'];
      this.tradeService.getAllTradesByUser(window.localStorage.getItem("userID")).then((strategies:any) => {
        console.log(strategies);
        this.dataSource = new MatTableDataSource(strategies.result);
        this.setDataSourceAttributes();
      })
    }

  }
  setDataSourceAttributes() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }
  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

}
