import { AbstractControl } from '@angular/forms';

export class ValidityValidator {
	/**
	 * Check matching password with confirm password
	 * @param control AbstractControl
	 */
	static CheckValidity(control: AbstractControl) {
		const validity_start = control.get('validity_start').value;

		const validity_end = control.get('validity_end').value;

		if (validity_start > validity_end) {
			control.get('validity_end').setErrors({ValidityError: true});
		} else {
			return null;
		}
	}
}
