import { Component, OnInit, ViewChild } from '@angular/core';
import { CreateUserComponent } from './create-user/create-user.component';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatDialog } from '@angular/material/dialog';
import { MatTableDataSource } from '@angular/material/table';
import { UserService } from 'src/app/service/user.service';
import { AssignStrategyComponent } from './assign-strategy/assign-strategy.component';

@Component({
  selector: 'kt-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.scss']
})
export class UsersComponent implements OnInit {
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) set matPaginator(mp: MatPaginator) {
    this.paginator = mp;
  }
  dataSource: MatTableDataSource<any>;
  displayedColumns = ['idusers','username', 'email', 'role', 'description', "assign"];
  pageIndex: number = 0;
  length: number = 10;

  constructor(
    public dialog: MatDialog,
    private userService: UserService,

    
  ) { }

  ngOnInit(): void {
    this.userService.getAllUsers().then((users:any) => {
      console.log(users);
      this.dataSource = new MatTableDataSource(users.result);
      this.setDataSourceAttributes();
    })
  }

  setDataSourceAttributes() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  create_user(){
    const dialogRef = this.dialog.open(CreateUserComponent, { data: {} });
    dialogRef.afterClosed().subscribe(result => {
      this.ngOnInit();
      // window.alert("Successfully saved!")
    })
  }

  onPaginateChange(event) {
    this.pageIndex = event.pageIndex;
    this.length = event.pageSize;
  }

  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

  userDetail(user){
    console.log(user);
    const dialogRef = this.dialog.open(CreateUserComponent, { data: {user} });
    dialogRef.afterClosed().subscribe(result => {
      this.ngOnInit();
      // window.alert("Successfully saved!")
    })
  }

  assign(user){
    console.log(user);
    const dialogRef = this.dialog.open(AssignStrategyComponent, { data: user });
    dialogRef.afterClosed().subscribe(result => {
      this.ngOnInit();
      // window.alert("Successfully saved!")
    })
  }



}
